.s-product-about {
    position: relative;
    padding: rem(28) 0;

    @media (min-width: 1024px) {
        padding: rem(75) 0;
    }

    &__wrap {
        position: relative;

        @media (min-width: 768px) {
            display: flex;
            justify-content: space-between;
            height: 100%;
            flex-direction: column;
            gap: rem(110);
        }

        @media (min-width: 1600px) {
            gap: rem(300);
        }

        @media (min-width: 1920px) {
            gap: rem(270);
        }
    }

    &__slogan {
        max-width: rem(310);
        margin-bottom: rem(40);
        font-size: rem(35);
        font-weight: 500;
        line-height: rem(34);
        color: rgba(255, 255, 255, 0.2);
        letter-spacing: -0.03em;

        @media (min-width: 992px) {
            max-width: rem(620);
            padding-top: rem(26);
            font-size: rem(70);
            line-height: rem(69);
        }

        @media (min-width: 1600px) {
            max-width: rem(890);
            padding-top: rem(43);
            font-size: rem(100);
            line-height: rem(98);
        }

        @media (min-width: 1920px) {
            max-width: rem(1250);
            padding-top: rem(80);
            font-size: rem(120);
            line-height: rem(118);
        }
    }

    &__logo {
        display: none;
    }

    &__title {
        margin-bottom: rem(5);
        font-size: rem(19);
        font-weight: 400;
        line-height: rem(22);
        text-align: center;
        color: #ceced0;

        @media (min-width: 768px) {
            text-align: left;
        }

        @media (min-width: 992px) {
            margin-bottom: rem(45);
            font-size: rem(35);
            line-height: rem(41);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(57);
            font-size: rem(38);
            line-height: rem(45);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(30);
            font-size: rem(40);
            line-height: rem(47);
        }
    }

    &__image {
        position: relative;
        width: rem(462);
        height: rem(252);
        margin-bottom: rem(17);

        @media (min-width: 575px) {
            width: rem(659);
            height: rem(359);
        }

        @media (min-width: 768px) {
            position: absolute;
            top: 0;
            right: 0;
        }

        @media (min-width: 992px) {
            right: rem(-150);
            width: rem(776);
            height: rem(422);
        }

        @media (min-width: 1600px) {
            right: rem(-50);
            width: rem(1223);
            height: rem(673);
        }

        img {
            position: absolute;
            top: 0;
            right: -5%;
            object-fit: contain;
            width: rem(462);
            height: rem(252);

            @media (min-width: 410px) {
                right: -30%;
                width: rem(659);
                height: rem(270);
            }

            @media (min-width: 768px) {
                right: -30%;
                width: rem(659);
                height: rem(359);
            }

            @media (min-width: 992px) {
                width: rem(776);
                height: rem(422);
            }

            @media (min-width: 1440px) {
                right: -40%;
            }

            @media (min-width: 1600px) {
                width: rem(1223);
                height: rem(673);
            }
        }
    }

    &__info {
        position: relative;
        z-index: 1;

        @media (min-width: 768px) {
            max-width: rem(310);
        }

        @media (min-width: 992px) {
            max-width: 100%;
        }
    }

    &__list {
        @media (min-width: 992px) {
            display: flex;
            align-items: center;
            max-width: 100%;
            gap: rem(44);
        }
    }

    &__item {
        &-text {
            margin-bottom: rem(5);
            font-size: rem(13);
            font-weight: 300;
            line-height: rem(15);
            text-align: center;
            color: #656669;

            @media (min-width: 768px) {
                margin-bottom: rem(15);
                font-size: rem(15);
                line-height: rem(18);
                text-align: left;
            }

            @media (min-width: 992px) {
                margin-bottom: 0;
                font-size: rem(17);
                line-height: rem(20);
            }

            @media (min-width: 1600px) {
                font-size: rem(19);
                line-height: rem(22);
            }

            @media (min-width: 1920px) {
                font-size: rem(21);
                line-height: rem(25);
            }
        }
    }

    &__registry {
        z-index: 1;
        font-size: rem(10);
        font-weight: 400;
        line-height: rem(12);

        @media (max-width: 574px) {
            position: absolute;
            top: rem(138);
            right: rem(15);
        }

        @media (min-width: 768px) {
            margin-top: rem(30);
            font-size: rem(15);
            font-weight: 300;
            line-height: rem(18);
        }

        @media (min-width: 992px) {
            margin-top: 0;
            margin-left: auto;
            font-size: rem(17);
            line-height: rem(20);
        }

        @media (min-width: 1600px) {
            font-size: rem(19);
            line-height: rem(22);
        }

        @media (min-width: 1920px) {
            font-size: rem(21);
            line-height: rem(25);
        }
    }
}
