.s-product-characteristics {
    position: relative;
    margin-top: rem(35);
    margin-bottom: rem(70);
    color: #ceced0;

    @media (min-width: 992px) {
        margin-bottom: rem(90);
    }

    &__bg {
        position: absolute;
        bottom: 0;
        left: 0;
        width: rem(420);
        height: rem(480);
        background-repeat: no-repeat;
        background-size: contain;

        @media (min-width: 768px) {
            width: rem(603);
        }

        @media (min-width: 992px) {
            width: rem(920);
            height: rem(740);
        }

        @media (min-width: 1440px) {
            width: rem(1060);
            height: rem(923);
        }

        @media (min-width: 1920px) {
            width: rem(1135);
        }
    }

    &__content {
        z-index: 1;
        position: relative;
        display: flex;
        align-items: flex-end;
        flex-direction: column;
    }

    &__items {
        display: flex;
        margin-bottom: rem(25);
        flex-direction: column;
        gap: rem(50);

        @media (min-width: 768px) {
            width: 50%;
            margin-bottom: rem(38);
            gap: rem(38);
        }

        @media (min-width: 992px) {
            display: flex;
            width: 100%;
            margin-bottom: rem(125);
            gap: rem(50);
            flex-direction: row;
            flex-wrap: wrap;
        }

        @media (min-width: 1440px) {
            justify-content: flex-end;
        }

        @media (min-width: 1440px) {
            margin-bottom: rem(145);
            gap: rem(110);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(220);
            gap: rem(140);
        }
    }

    &__row {
        &-title {
            margin-bottom: rem(15);
            font-size: rem(17);
            line-height: rem(20);
            text-transform: uppercase;

            @media (min-width: 992px) {
                margin-bottom: rem(38);
                font-size: rem(23);
                line-height: rem(29);
            }

            @media (min-width: 1440px) {
                margin-bottom: rem(99);
                font-size: rem(35);
                line-height: rem(41);
            }

            @media (min-width: 1920px) {
                margin-bottom: rem(93);
                font-size: rem(40);
                line-height: rem(47);
            }
        }

        &-items {
            display: flex;
            flex-wrap: wrap;
            gap: rem(50);

            @media (min-width: 1024px) {
                gap: rem(75);
            }

            @media (min-width: 1920px) {
                gap: rem(110);
            }
        }
    }

    &__item {
        max-width: rem(210);

        @media (min-width: 1024px) {
            max-width: 100%;
        }

        &-title {
            margin-bottom: rem(5);
            font-size: rem(10);
            line-height: rem(12);

            @media (min-width: 992px) {
                min-height: rem(43);
                margin-bottom: rem(22);
                font-size: rem(15);
                line-height: rem(18);
            }

            @media (min-width: 1440px) {
                margin-bottom: rem(37);
                font-size: rem(18);
                line-height: rem(21);
            }
        }

        &-text {
            font-size: rem(25);
            font-weight: 700;
            line-height: rem(29);
            text-transform: uppercase;

            @media (min-width: 992px) {
                font-size: rem(40);
                line-height: rem(47);
            }

            @media (min-width: 1440px) {
                font-size: rem(60);
                line-height: rem(70);
            }
        }
    }

    &__bottom {
        display: flex;
        justify-content: flex-end;
    }

    &__text {
        max-width: rem(258);
        margin-bottom: rem(230);
        font-size: rem(12);
        line-height: 1.5;

        @media (min-width: 768px) {
            max-width: rem(368);
        }

        @media (min-width: 992px) {
            max-width: rem(470);
            margin-bottom: rem(370);
        }

        @media (min-width: 1024px) {
            font-size: rem(15);
            line-height: 1.7;
        }

        @media (min-width: 1440px) {
            max-width: rem(560);
            margin-bottom: rem(430);
            font-size: rem(19);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(375);
        }

        p {
            &:not(:last-child) {
                margin-bottom: rem(17);
            }
        }
    }
}
