.s-product-tech {
    margin-bottom: rem(100);
    padding: rem(35) 0;

    @media (min-width: 768px) {
        margin-bottom: rem(65);
    }

    @media (min-width: 1024px) {
        margin-bottom: rem(110);
    }

    @media (min-width: 1600px) {
        margin-bottom: rem(170);
    }

    @media (min-width: 1920px) {
        margin-bottom: rem(200);
    }

    .l-default {
        padding-right: 0;

        @media (min-width: 1600px) {
            padding-left: rem(240);
        }

        @media (min-width: 1920px) {
            padding-left: rem(270);
        }
    }

    .title {
        margin-bottom: rem(45);

        @media (min-width: 1024px) {
            margin-bottom: rem(86);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(100);
        }
    }

    &__info {
        margin-bottom: rem(35);

        @media (min-width: 768px) {
            margin-bottom: rem(55);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(60);
        }

        &._show {
            .s-product-tech__item {
                &:nth-child(n + 6) {
                    display: flex;
                }
            }
        }

        &:not(_show) {
            .s-product-tech__item:nth-child(5) {
                border-bottom: 0;
            }
        }
    }

    &__item {
        overflow: hidden;
        position: relative;
        display: flex;
        align-items: flex-start;
        padding: rem(7) 0;
        font-size: rem(10);
        font-weight: 300;
        line-height: rem(12);
        color: $white;

        @media (min-width: 768px) {
            padding: rem(12) 0;
            font-size: rem(12);
            line-height: rem(14);
        }

        @media (min-width: 1024px) {
            padding: rem(14) 0;
            font-size: rem(17);
            line-height: rem(20);
        }

        @media (min-width: 1600px) {
            font-size: rem(19);
            line-height: rem(22);
        }

        &:not(:last-child) {
            border-bottom: rem(1) solid #656669;
        }

        &:nth-child(n + 6) {
            display: none;
        }

        &-name {
            width: rem(147);
            padding-right: rem(15);
            flex: none;

            @media (min-width: 1024px) {
                width: rem(230);
            }
        }

        &-values {
            position: relative;
            width: 100%;
            padding-right: rem(19);
            padding-left: rem(19);

            @media (min-width: 768px) {
                padding-left: rem(43);
            }

            @media (min-width: 1024px) {
                padding-left: rem(47);
            }

            @media (min-width: 1600px) {
                padding-left: rem(58);
            }

            &::before {
                position: absolute;
                top: -30px;
                left: 0;
                width: rem(1);
                height: calc(100% + 100px);
                content: "";
                background-color: #656669;
            }

            span {
                display: block;

                &:not(:last-child) {
                    margin-bottom: rem(5);

                    @media (min-width: 1600px) {
                        margin-bottom: rem(7);
                    }
                }
            }

            p {
                @media (min-width: 1600px) {
                    margin-bottom: rem(7);
                }
                &:not(:last-child) {
                    margin-bottom: rem(5);
                }
            }

            ul {
                margin: 0;
                padding: 0;
                list-style: none;

                li {
                    @media (min-width: 1600px) {
                        margin-bottom: rem(7);
                    }
                    &:not(:last-child) {
                        margin-bottom: rem(5);
                    }
                }
            }
        }

        &:nth-child(even) {
            background: rgba(217, 217, 217, 0.07);
        }
    }

    &__buttons {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-right: rem(15);
        flex-wrap: wrap;
        gap: rem(18);

        @media (min-width: 768px) {
            justify-content: flex-end;
            flex-wrap: nowrap;
        }

        @media (min-width: 1024px) {
            justify-content: center;
            gap: rem(30);
        }

        @media (min-width: 1600px) {
            gap: rem(20);
        }
    }

    &__button {
        height: rem(44);
        max-width: initial;
        padding-right: rem(14);
        padding-left: rem(14);

        @media (min-width: 768px) {
            width: max-content;
            min-width: rem(167);
        }

        &:nth-child(1),
        &:nth-child(2) {
            width: 47%;

            @media (min-width: 768px) {
                width: max-content;
            }
        }

        &:nth-child(3) {
            width: 100%;

            @media (min-width: 768px) {
                width: max-content;
            }
        }

        &:hover {
            border: rem(1) solid $red;
        }
    }
}
