.s-product-benefits {
    margin-bottom: rem(35);

    @media (min-width: 1024px) {
        margin-bottom: rem(60);
    }

    @media (min-width: 1440px) {
        margin-bottom: rem(80);
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: rem(20);

        @media (min-width: 768px) {
            gap: rem(40);
        }

        @media (min-width: 1024px) {
            gap: rem(45);
        }

        @media (min-width: 1440px) {
            gap: rem(30);
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__item {
        overflow: hidden;
        position: relative;
        width: 100%;
        max-width: rem(460);
        min-height: rem(280);
        margin: 0 auto;
        color: #ceced0;
        background-image: url(/img/product-benefit/bg.png);
        background-color: rgba(47, 47, 47, 0.97);
        backdrop-filter: blur(rem(2));

        @media (min-width: 768px) {
            min-height: rem(427);
        }

        @media (min-width: 1024px) {
            max-width: rem(580);
            min-height: rem(536);
        }

        @media (min-width: 1440px) {
            max-width: 100%;
            min-height: rem(661);
        }

        &:nth-child(1) {
            .s-product-benefits__item-image {
                top: 0;
                right: 0;

                img {
                    max-height: rem(128);

                    @media (min-width: 768px) {
                        max-height: rem(236);
                    }

                    @media (min-width: 1024px) {
                        max-height: rem(580);
                    }

                    @media (min-width: 1440px) {
                        max-height: rem(312);
                    }
                }
            }
        }

        &:nth-child(2) {
            .s-product-benefits__item {
                display: flex;
                align-items: flex-start;
                justify-content: flex-end;

                &-info {
                    @media (min-width: 1600px) {
                        padding-left: rem(220);
                    }
                }

                &-image {
                    bottom: 0;
                    left: 0;

                    img {
                        max-height: rem(208);

                        @media (min-width: 768px) {
                            max-height: rem(427);
                        }

                        @media (min-width: 1024px) {
                            max-height: rem(580);
                        }
                    }
                }
            }
        }

        &:first-child {
            display: flex;
            justify-content: flex-end;
            flex-direction: column;
        }

        &-image {
            position: absolute;
        }

        &-info {
            z-index: 1;
            position: relative;
            max-width: 90%;
            padding: rem(23);

            @media (min-width: 768px) {
                padding: rem(40) rem(30);
            }

            @media (min-width: 1024px) {
                padding: rem(45);
            }

            @media (min-width: 1440px) {
                padding: rem(60);
            }
        }

        &-title {
            margin-bottom: rem(5);
            font-size: rem(19);
            line-height: rem(22);
            text-transform: uppercase;

            @media (min-width: 768px) {
                margin-bottom: rem(15);
                font-size: rem(25);
                line-height: rem(29);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(20);
            }

            @media (min-width: 1440px) {
                font-size: rem(38);
                line-height: rem(45);
            }
        }

        &-text {
            font-size: rem(10);
            line-height: 1.5;
            letter-spacing: -0.02em;

            @media (min-width: 768px) {
                font-size: rem(12);
            }

            @media (min-width: 1024px) {
                font-size: rem(15);
            }

            @media (min-width: 1440px) {
                font-size: rem(19);
            }

            p {
                &:not(:last-child) {
                    margin-bottom: rem(10);

                    @media (min-width: 768px) {
                        margin-bottom: rem(15);
                    }

                    @media (min-width: 1024px) {
                        margin-bottom: rem(20);
                    }

                    @media (min-width: 1440px) {
                        margin-bottom: rem(40);
                    }
                }

                &:nth-child(2),
                &:nth-child(3) {
                    padding-left: rem(30);

                    @media (min-width: 1024px) {
                        padding-left: rem(40);
                    }

                    @media (min-width: 1440px) {
                        padding-left: rem(60);
                    }
                }
            }
        }
    }
}
