.s-product-content {
    &__text {
        max-width: rem(540);
        margin: 0 auto;
        padding: rem(16) rem(30) 0;
        font-size: rem(25);
        line-height: 1.3;
        color: #424346;

        @media (min-width: 768px) {
            font-size: rem(35);
        }

        @media (min-width: 1024px) {
            max-width: rem(610);
            font-size: rem(45);
        }

        @media (min-width: 1440px) {
            max-width: rem(960);
            font-size: rem(70);
        }
    }

    &__image {
        img {
            width: 100%;
            height: auto;
        }
    }
}
